@charset "utf-8";
/*------------------------------------*\
	Reset CSS
		- based Eric-Meyer's CSS Reset
\*------------------------------------*/
html,body,div,span,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,abbr,address,cite,code,del,dfn,em,img,ins,kbd,q,samp,small,strong,sub,sup,var,b,i,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,figcaption,figure,footer,header,main,hgroup,menu,nav,section,summary,time,mark,audio,video{
	background:transparent;
	margin:0;
	padding:0;
	border:0;
	max-width:100%
}
article,aside,details,figcaption,figure,footer,header,main,menu,nav,section{
	display:block
}
body{
	background-color:#FAFAFA;
	line-height:1.8;
	font-family:'Apple SD Gothic Neo','Apple Gothic','나눔고딕','Nanum Gothic','맑은 고딕','Malgun Gothic';
	font-size:100%
}
ul,ol{
	margin-left:1.75rem
}
ol ol{
	list-style-type:lower-roman
}
ol ol ol{
	list-style-type:lower-alpha
}
blockquote,q{
	quotes:none
}
pre{
	display:inline-block
}
blockquote:before,blockquote:after,q:before,q:after{
	content:'';
	content:none
}
a{
	color:inherit
}
ins{
	color:#000;
	text-decoration:none
}
del{
	text-decoration:line-through
}
abbr[title],dfn[title]{
	border-bottom:1px dotted;
	cursor:help
}
table{
	table-layout:fixed;
	border-collapse:collapse;
	border-spacing:0
}
table,tr,th,td{
	border-collapse:collapse;
	border-spacing:0;
	empty-cells:show
}
input,select{
	margin:0;
	padding:0;
	vertical-align:middle
}
button{
	font:inherit;
	margin:0;
	padding:0;
	border:0;
	background:transparent;
	cursor:pointer
}
img{
	max-width:100%
}
img[height]{
	height:auto !important
}
em{
	margin-right:.5em
}
.hidden-accessible{
	overflow:hidden;
	clip:rect(1px,1px,1px,1px);
	position:absolute;
	width:1px;
	height:1px
}
.clearfix:after{
	clear:both;
	content:'';
	display:table;
	height:0;
	overflow:hidden;
	visibility:hidden
}
.fix-justify:before{
	content:'';
	display:inline-block;
	width:100%;
	height:0;
	overflow:hidden
}
html,body{
	height:100%;
	min-height:100%
}
body{
	-webkit-box-sizing:border-box;
	-moz-box-sizing:border-box;
	box-sizing:border-box;
	padding:1em
}



button {
  position: relative;
  display: block;
  min-width: 88px;
  padding: 12px 20px;
  height: 3em;
  margin: 2em;
  border: none;
  outline: none;
  letter-spacing: 0;
  background: #dfdfdf;
  cursor: pointer;
  overflow: hidden;
  border-radius: 3px;
  transition: all 0.3s ease-in-out;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.12), 0 1px 2px 0 rgba(0,0,0,0.24);
}
.btn-rp {
  background: #fff;
  border-color: #cdcdcd;
  &:hover {
    background: #f5f5f5;
    border-color: #f5f5f5;
  }
  &:focus, &:active {
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.15), 0 3px 6px 0 rgba(0,0,0,0.23);
    background: #e6e6e6;
  }
}

.ripple {
  position: absolute;
  background: rgba(0,0,0, 0.25);
  border-radius: 100%;
  pointer-events: none;
  transform: scale(0);
}
.ripple.show {
  animation: ripple 0.45s ease-out;
}

@keyframes ripple {
  to {
    transform: scale(1.5);
    opacity: 0;
  }
}
